/* latin-ext */
@font-face {
    font-family: 'Ranchers';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local('Ranchers'), local('Ranchers-Regular'), url(https://fonts.gstatic.com/s/ranchers/v8/zrfm0H3Lx-P2Xvs2ArDRBi_NXQ.woff2) format('woff2');
    unicode-range: U+0100-024F, U+0259, U+1E00-1EFF, U+2020, U+20A0-20AB, U+20AD-20CF, U+2113, U+2C60-2C7F, U+A720-A7FF;
  }
  /* latin */
  @font-face {
    font-family: 'Ranchers';
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local('Ranchers'), local('Ranchers-Regular'), url(https://fonts.gstatic.com/s/ranchers/v8/zrfm0H3Lx-P2Xvs2ArDfBi8.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
  }


body {
    background-color: #08AEEA;
    background-repeat: no-repeat;
    background-image: linear-gradient(0deg, #08AEEA 0%, #2AF598 100%);
    height: 880px;
}

h1 {
    font-family: 'Ranchers', cursive;
}
